body {
    background-color: #cdecf2;
}

@font-face {
    font-family: 'Roboto Mono';
    font-weight: normal;
    font-style: normal;
}

html, body, div, canvas {
    margin: 0;
    padding: 0;
}

.controls {
  position: absolute;
  padding-top: 1em;
  padding-left: 1em;
  top: 0;
  left: 0;
  background: rgba(200, 200, 200, 0.6);
  margin: 2em;
  border-radius: 1em;
  border: 1px solid white;
  width: 20em;
  height: 90vh;
  font-family: 'Arial';
  font-weight: bold;
}

button {
  height: 3em;
  width: 10em;
  margin: 1em;
  margin-left: 1em;
  margin-right: 1em;
  font-weight: bold;
  font-size: 0.75em;
  cursor: pointer;
  color: white;
  border: 1px solid white;
  border-radius: 5px;
  background-color: rgba(100, 100, 100, 1);
}

input {
  height: 3em;
  width: 12.75em;
  margin: 0.5em;
  margin-left: 1em;
  padding-left: 0.75em;
  padding-right: 0.5em;
      display: block;

    font-family: Roboto Mono, Monaco, monospace;
  font-size: 0.75em;
  cursor: pointer;
  color: white;
  border: 1px solid white;
  background-color: black;
}

button:hover {
  color: black;
  background-color: #00baff;
}

button:focus {
  border: 1px solid #00baff;
}

button:active {
  background-color: #1f1f1f;
  color: white;
}

#hamburger {
  height: 4em;
}

.menuHeader {
  margin-top: 2em;
}

.container {
  display: inline-block;
  cursor: pointer;
}

.bar1, .bar2, .bar3 {
  width: 35px;
  height: 5px;
  background-color: #5f5f5f;
  margin: 6px 0;
  transition: 0.4s;
}

.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9.5px, 6px);
  transform: rotate(-45deg) translate(-9.5px, 6px);
}

.change .bar2 {opacity: 0;}

.change .bar3 {
  -webkit-transform: rotate(45deg) translate(-9.5px, -6px);
  transform: rotate(45deg) translate(-9.5px, -6px);
}